import React, {useState, useEffect} from 'react';
import api from '../../services/api';
import "./style.css";
import logo from "../../res/imgs/wherev3-1.png";


export default function Signup() {

    const [companies, setCompanies] = useState([])

    const [photoUrl, setPhotoUrl] = useState("");
    const [fantasia, setFantasia] = useState('');
    const [endereco, setEndereco] = useState('');
    const [telefone, setTelefone] = useState('');
    const [email, setEmail] = useState('');
    const [website, setWebsite] = useState('');
    const [segmentos, setSegmentos] = useState('');
    const [senha, setSenha] = useState('');
    const [confSenha, setConfSenha] = useState('');
    const [descricao, setDescricao] = useState('');

    

    useEffect( () => {
        async function loadCompanies() {
            const response = await api.get("/companies");
            const data = response.data;
        
            setCompanies(data);
        }
        loadCompanies();
    }, [] )

    async function compSignUp(e) {
        //e.preventDefault();
        if(senha === confSenha && senha.length >= 8){

        try{    
        const response = await api.post("/auth/register", {
           photoUrl,
           fantasia,
           endereco,
           telefone,
           email,
           website,
           segmentos,
           senha,
           descricao
        })
            setPhotoUrl("");
            setFantasia("");
            setEndereco("");
            setTelefone("");
            setEmail("");
            setWebsite("")
            setSenha("");
            setSegmentos("");
            setConfSenha("");
            setDescricao("");
        
        setCompanies([...companies , response.data]);
        localStorage.setItem("userToken", response.data.token);
        alert("Obrigado cadastrar a/o " + fantasia + " conosco. Seja bem vindo! =)  ");
        
    }catch(err){
        alert(err);
    }
        }else{
            alert("As senhas não correspondem ou não é segura (Deve conter no mínimo 8 caracteres)")
        } 
    }
    return(
        <>
        <div className="signup">
            <aside>
                <span className="signup-title" >
            {//<img id="formLogo" src={logo} alt="where-logo"/>
            }Cadastre-se Grátis</span>
            <form onSubmit={compSignUp}>
              <div className="v_inputs">
                <label htmlFor="n_fantasia">Nome Fantasia *</label>
                <input
                 name="n_fantasia"
                 id="n_fantasia"
                 required
                 value={fantasia}
                 onChange={e => {setFantasia(e.target.value)}}
                  />
                
                <label htmlFor="endereco">Endereço
                <a style={{fontWeight: "normal"}}> (Se trabalhar somente com entregas,<br/> digite 'Delivery')*</a>
                </label>                
                <input 
                placeholder="Rua, número - Bairro"
                name="endereco"
                id="endereco"
                required
                value={endereco}
                 onChange={e => {setEndereco(e.target.value)}}
                />

                <label htmlFor="telefone">Telefone *</label>
                <input 
                placeholder="ex: (32) 9999-9999"
                name="telefone"
                id="telefone"
                required
                value={telefone}
                onChange={e => {setTelefone(e.target.value)}}
                />

                <label htmlFor="email">Email *</label>
                <input 
                name="email"
                type="email"
                id="email"
                required
                value={email}
                onChange={e => {setEmail(e.target.value)}}
                />


                <label htmlFor="email">Website *</label>
                <input 
                placeholder="ex: https://www.seusite.com.br/"
                name="website"
                type="website"
                id="website"
                required
                value={website}
                onChange={e => {setWebsite(e.target.value)}}
                />


                <label htmlFor="segmentos">Segmentos</label>
                <input 
                name="segmentos" 
                id="segmentos" 
                placeholder="ex: Tecnologia, Suporte, Informatica"
                value={segmentos}
                 onChange={e => {setSegmentos(e.target.value)}}
                />
                
                <label htmlFor="senha">Senha *</label>
                <input 
                name="senha"
                type="password" 
                id="senha"
                required
                value={senha}
                onChange={e => {setSenha(e.target.value)}}
                />

                <label htmlFor="conf_senha">Confirmar senha *</label>
                <input 
                name="conf_senha" 
                type="password" 
                id="conf_senha" 
                required
                value={confSenha}
                onChange={e => {setConfSenha(e.target.value)}}
                />
                

                <label htmlFor="descricao">Adicione uma breve descrição *</label>
                <input 
                name="descricao" 
                type="text" 
                id="descricao_input" 
                required
                value={descricao}
                onChange={e => {setDescricao(e.target.value)}}
                />

              </div>
              <p>Você pode alterar esses dados pelo aplicativo</p>
                <p>Ao clicar em Cadastrar, você concorda com os<a href="/terms" id="linkToTermos">@Termos de uso</a></p>


              <button type="submit">Cadastrar</button>
            </form>

            </aside>
            <div className="text-confira"> <strong>Confira quem já está conosco: </strong></div>

            <main>
                {<ul>
                {companies.map(company => (
                    <li key={company._id} className="company-key">
                    <header>
                        <div className="header-wrapper">
                        <img src={company.photo_url} alt="company-logo"/>
                        </div>
                        <div className="block-header">
                        <p><strong>{company.fantasia}</strong></p>
                        <p><span>{company.endereco}</span></p>
                        <p><span>{company.telefone}</span></p>
                        <p><span>{company.email}</span></p>
                        <p><span>{company.website}</span></p>
                        </div>
                    </header>
                </li>
                ))}           
               </ul>
                }
            </main>
        </div>
        </>
    )

}