import React from "react";
import logo from "../../res/imgs/wherev3-1.png"
import "./style.css";



export default function MobileHeader() {
    return (
        <>
        <div className="mobile-trigger">
        <a href="/">    
       <img src={logo} alt="Where Logo" width="100%" height="100%"/>
       </a>
        <span onClick={() => {document.getElementById("mheader").style.width = "300px"}}> ... </span>           
        </div>

        <div id="mheader" className="mheader-container">
            <div className="mobile-menu-header-wrapper">
            <p onClick={() => {document.getElementById("mheader").style.width = "0px"}}>
                X
            </p>     
            </div>
            <div className="mheader-nav">
            <a href="callto:+5561999486774" id="mheaderContact"> Contate-nos </a>
            {//<a href="/signup" id="mheaderContact"> Cadastre-se Grátis </a>
            }
            </div>
        </div>
        </>
    )




}